*{
    margin:0;
    padding:0;
    box-sizing:border-box;
}
#root {
    display:flex;
    flex-direction: column;
    align-items: center;
}

.App {
    width: 800px;
}
.post{
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    margin-top:15px;
    justify-content: space-between;
    align-items: center;
}

.post__btns {
    display: flex;
}

.item-enter {
    transform:translateX(-350px);
}
.item-enter-active {
    transform:translateX(0px);
    transition:all 500ms ease-in;
}
.item-exit{
    opacity:1}
.item-exit-active{
    transform:translateX(-350px);
    transition:all 500ms ease-in;
}

.page__wrapper {
    margin-top:30px;
    display:flex;
}

.page{
    padding:10px;
    border:1px solid teal;
    cursor: pointer;
    margin: 0px 1px;
}
.page__current {
background-color: teal;
color:whitesmoke;
font-weight: bold;
}

.navbar{
    height: 50px;
    width: 100vw;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background-color: teal;
    color:whitesmoke;
}

.navbar__links{
    margin-left: auto;

}